import React from 'react';
import { Modal } from 'react-bootstrap';
import ModalComponent from './modal';
import Collapsible from 'react-collapsible';
import { graphql, useStaticQuery } from 'gatsby';
import Img from 'gatsby-image';
import styled from 'styled-components';

import openIcon from '../../images/icons/collapsible-open.svg';
import closedIcon from '../../images/icons/collapsible-closed.svg';

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
  box-shadow: 0px 32px 102px rgba(23, 9, 40, 0.36);
  border-radius: 16px;
`;

const PopupChannels = ({ show, setShow }) => {

  const data = useStaticQuery(graphql`
    query Channels {
      bg: file(relativePath: {eq: "channels/popup-channels-bg.png"}) {
        childImageSharp {
          fluid(maxWidth: 2000) {
            src
          }
        }
      }
      polythematicPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-polythematic"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      addOnsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-add-ons"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      documentaryPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-documentary"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      hobbyPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-hobby"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      kidsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-kids"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      moviesPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-movies"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      musicPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-music"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      newsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-news"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      sportPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-sport"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      xxxPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-xxx"}}){
        edges {
          node {
            childImageSharp {
              fluid(maxHeight: 40) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  `)

  const allData = [
    {
      title: 'Политематични',
      channels: data.polythematicPlaceholder,
    },
    {
      title: 'Новини',
      channels: data.newsPlaceholder,
    },
    {
      title: 'Филми',
      channels: data.moviesPlaceholder,
    },
    {
      title: 'Детски',
      channels: data.kidsPlaceholder,
    },
    {
      title: 'Спорт',
      channels: data.sportPlaceholder,
    },
    {
      title: 'Хоби и развлекателни',
      channels: data.hobbyPlaceholder,
    },
    {
      title: 'Музика',
      channels: data.musicPlaceholder,
    },
    {
      title: 'Документални',
      channels: data.documentaryPlaceholder,
    },
    {
      title: 'За възрастни (18+)',
      channels: data.xxxPlaceholder,
    },
    {
      title: 'Допълнителни',
      channels: data.addOnsPlaceholder,
    },

  ]

  return (
    <>
      <ModalComponent
        show={show}
        setShow={setShow}
      >
        <BackgroundWrapper bg={data.bg.childImageSharp.fluid.src}>
          <Modal.Header>
            <Modal.Title id="custom-modal-styling-title" className='w-100'>
              <div className='row w-100 d-flex justify-content-end'>
                <span className='icon popup-close' onClick={() => { setShow(false) }}></span>
              </div>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>

            <div className='px-sm-5'>
              <p>Виж всички твои</p>
              <h2 className='mb-4'>Любими ТВ програми</h2>
            </div>

            {allData.map(({ title, channels }, index) => (
              <ChannelSection
                key={title + index}
                title={title}
                channels={channels}
                isInitiallyOpen={index === 0}
              />
            ))}
          </Modal.Body>
        </BackgroundWrapper>
      </ModalComponent>
    </>
  );
}

const ChannelSection = ({ title, channels, isInitiallyOpen }) => {
  return (
    <>
      <Collapsible
        open={isInitiallyOpen}
        trigger={
          <CollapsibleHeader
            isOpen={false}
            title={title}
          />
        }
        triggerWhenOpen={
          <CollapsibleHeader
            isOpen={true}
            title={title}
          />
        }
      >
        <ChannelRow
          channels={channels}
        />
      </Collapsible>
    </>)
}

export const ChannelRow = ({ channels }) => {
  return (
    <>
      <div className='row'>
        {channels.edges.map(({ node }, index) => (
          <div key={index} className='col-4 col-sm-2 p-2 mb-5' style={{ maxHeight: 40 }}>
            <Img
              fluid={node.childImageSharp.fluid}
              style={{ maxHeight: 40 }}
              imgStyle={{ objectFit: "contain" }}
              alt='Канал'
            />
          </div>
        ))}
      </div>
    </>
  )
}

const CollapsibleHeader = ({ title, isOpen }) => {
  return (
    <>
      <div className='row w-100'>
        <div className='d-inline-block w-100'>
          <button className='clear'>
            <img src={isOpen ? openIcon : closedIcon} className='d-inline-block' alt='Тригър' />
            <h3 className={'d-inline-block pb-0'}>{title}</h3>
          </button>
          <hr className={'ml-5 collapsible channel-popup'} />
        </div>
      </div>

    </>
  )
}

export default PopupChannels;