import React, { useState } from 'react';
import Package from './package';
import packages from '../../constants/packages';
import { Link } from 'gatsby';
import Slider from 'react-slick';

const PackageSection = () => {
  const [isOn, setToggle] = useState(true);
  const [extendPackages, setExtendPackages] = useState(false);

  const combinedPackages = [packages.midPackage, packages.bigPackage, packages.xlPackage];
  const internetPackages = [packages.midInternetPackage, packages.bigInternetPackage, packages.xlInternetPackage];
  return (
    <>
      <section id='packages-hero' className='spacing-bottom'>
        <div className='container text-center'>
          <Toggle
            isOn={isOn}
            setOn={setToggle}
            leftText={'ТВ + Интернет'}
            leftShortText={'ТВ + Нет'}
            rightText={'Интернет'}
            rightShortText={'Нет'}
          />
          <div className='mt-5 reasons-slider'>
            <RenderPackages
              packageList={isOn ? combinedPackages : internetPackages}
              extend={extendPackages}
              setExtend={(v) => setExtendPackages(v)}
            />
          </div>
          <Link to='/configurator' className='btn btn-secondary mt-5'>Сравни пакетите</Link>
        </div>
      </section>
    </>
  )
}

const RenderPackages = ({ extend, setExtend, packageList }) => (
  <Slider {...settings}>
    {packageList.map((p, index) => (
      <div key={index} className='d-flex justify-content-center m-1'>
        <Package
          isScaledDown={index !== 1}
          packageInfo={p}
          extend={extend}
          setExtend={setExtend}
        />
      </div>
    ))}
  </Slider>
)

export const Toggle = ({ isOn, setOn, leftText, leftShortText, rightText, rightShortText }) => {
  return (
    <>
      <div className='d-inline-block toggle-wrapper' style={{ whiteSpace: 'nowrap' }}>
        <button className='clear' onClick={(_) => setOn(true)}>
          <span className={'toggle ' + (isOn && 'active overlap')}>
            <h3 className='d-inline d-sm-none'>{leftShortText}</h3>
            <h3 className='d-none d-sm-inline'>{leftText}</h3>
          </span>
        </button>
        <button className='clear' onClick={(_) => setOn(false)}>
          <span className={'toggle ' + (!isOn && 'active overlap left')}>
            <h3 className='d-inline d-sm-none'>{rightShortText}</h3>
            <h3 className='d-none d-sm-inline'>{rightText}</h3>
          </span>
        </button>
      </div>
    </>
  )
}

const settings = {
  dots: false,
  speed: 1000,
  arrows: false,
  infinite: false,
  slidesToShow: 3,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,
  responsive: [
    {
      breakpoint: 991,
      settings: {
        slidesToShow: 1,
        arrows: false,
        dots: true,
      }
    },
  ]
}

export default PackageSection;