import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import PopupChannels from '../shared/popup-channels';
import Popup from '../shared/popup';
import { Link } from 'gatsby';

import packageBottomImg from '../../images/packages/package-bottom.svg'

const PackageWrapper = styled.div`
  transform: scale(${props => props.isScaledDown ? 0.95 : 1});
`;

const PackageCard = styled.div`
  background: #FFFFFF;
  box-shadow: 0px 0px 38px rgba(17, 6, 29, 0.2);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  min-width: 350px;
  max-width: 355px;
`;

const PackageCardBottom = styled.div`
  min-width: 350px;
  max-width: 355px;
  background-image: url(${packageBottomImg});
  background-position: center;
  background-size: cover;
  background-position: top;
`;

const PackageHeader = styled.div`
  min-height: 8rem;
`;

const Package = ({ packageInfo, isScaledDown, extend, setExtend }) => {
  const [showChannels, setShowChannels] = useState(false);
  const [showInfo, setShowInfo] = useState(false);
  const infoIconRef = useRef(null);

  const isFull = !packageInfo.isOnlyInternet;

  return (
    <>
      <PackageWrapper isScaledDown={isScaledDown}>
        <div className='package'>
          <PackageCard className='py-2 col-12'>
            <div className='row d-flex justify-content-center align-items-center'>
              <PackageHeader className='text-center'>
                <img src={packageInfo.icon} alt='Икона на пакета' />
                <h3 className='my-3'>{packageInfo.title}</h3>
              </PackageHeader>
            </div>

            <div className='row py-3 b-bottom mx-2'>
              <div className={isFull ? 'col-6 text-center b-right' : 'col-12 text-center'}><p>Интернет<br /><span className='text-blue'>{packageInfo.internetSpeed}Mbp/s</span></p></div>
              <div className={isFull ? 'col-6 text-center' : 'd-none'}><p>Телевизия<br /><span className='text-blue'>{packageInfo.tvChannels} канала</span></p></div>
            </div>

            <div className='row pt-3 b-bottom mx-2 d-flex justify-content-center align-items-center'>
              <div className='col-6 row p-0'>
                <div className='col-6 p-0 d-flex justify-content-end align-items-center'>
                  <h3 className='price'><span className='text-blue'>{packageInfo.price}</span></h3>
                </div>
                <div className='col-6 p-0 m-0 row d-flex justify-content-start '>
                  <h3 className='price-coins'><span className='text-blue'>{packageInfo.priceCoins}</span></h3>
                  <h3 className='price-label pb-1'><span className='text-blue'>лв/мес</span></h3>
                </div>
              </div>
            </div>

            <div className='row py-4 b-bottom mx-2 d-flex justify-content-center align-items-center'>
              <p>Подходящ за:</p>
              <div className='row d-flex justify-content-center align-items-center w-100'>
                {packageInfo.icons.map((icon, index) => (
                  <div key={index} className='mx-3'>
                    <img src={icon} alt='Пакета е подходящ' />
                  </div>
                ))}
              </div>
            </div>

            <div className='row py-2 b-bottom mx-2'>
              {isFull && (
                <div className='row w-100 b-bottom-faded d-flex justify-content-between align-items-center'>
                  <p className='m-1'>Цифрови канала</p>
                  <p className='m-1'>
                    <span className='text-blue'>{packageInfo.tvChannels}</span>
                    <button className='clear ml-1 p-0' onClick={() => setShowChannels(true)}>
                      <span className='icon package view-channels'></span>
                    </button>
                  </p>
                </div>
              )}

              <div className={'row w-100 d-flex justify-content-between align-items-center ' + (isFull && 'b-bottom-faded')} >
                <p className='m-1'>Интернет скорост</p>
                <p className='m-1'><span className='text-blue'>{packageInfo.internetSpeed}</span></p>
              </div>

              {isFull && (
                <>
                  <div className='row w-100 b-bottom-faded d-flex justify-content-between align-items-center'>
                    <p className='m-1'>HD канала</p>
                    <p className='m-1'><span className='text-blue'>{packageInfo.hdChannels}</span></p>
                  </div>
                  <div className='row w-100 d-flex justify-content-between align-items-center'>
                    <p className='m-1'>HD цифров приемник
                  <span
                        ref={infoIconRef}
                        onMouseEnter={(e) => setShowInfo(true)}
                        onMouseLeave={(e) => setShowInfo(false)}
                        className='icon info'>
                      </span>
                    </p>
                    <p className='m-1'><span className='text-blue'>{packageInfo.hdFetcher}</span></p>
                  </div>
                </>
              )}
            </div>

            {!extend &&
              (<Link to={'/checkout'} className={'my-2 btn ' + (packageInfo.isPrimaryPackage ? 'btn-primary green' : 'btn-secondary-no-outline')}>Избери пакет</Link>)}

            {extend && (
              <div className='row pt-4 d-flex justify-content-center align-items-center'>
                <div className='row p-0 m-0 w-100'>
                  <ul className='package'>
                    {packageInfo.packageFeatures.map(({ text, active }, index) => (
                      <li key={index + text} className={'p-0 m-0 ' + (active ? 'active-feature' : 'non-active-feature')}>
                        <p className='p-1 m-0 text-left'>{text}</p>
                      </li>
                    ))}
                  </ul>
                </div>
                <Link to={'/checkout'} className={'my-2 btn ' + (packageInfo.isPrimaryPackage ? 'btn-primary green' : 'btn-secondary-no-outline')}>Избери пакет</Link>
              </div>
            )}

          </PackageCard>
          <PackageCardBottom className='col-12 m-0 p-0 py-1 text-center'>
            <button
              className='clear link'
              onClick={(_) => { setExtend(!extend) }}
            >
              {extend ? 'Скрий' : 'Покажи'}
            </button>
          </PackageCardBottom>
        </div>
      </PackageWrapper>

      <Popup
        showTelephonePopover={showInfo}
        target={infoIconRef}
      >
        <div
          onMouseLeave={(_) => setShowInfo(false)}
        >
          <div className='phone-pop-up bg-white col-12 col-md-3 px-5 py-3 mx-5'>
            <p>{packageInfo.hdFetcherInfo}</p>
          </div>
        </div>
      </Popup>

      <PopupChannels
        show={showChannels}
        setShow={(v) => { setShowChannels(v) }}
      />
    </>
  )
}

export default Package;